import React, { useEffect, useRef } from 'react';
import './InvitationText.css';  // CSS  

const InvitationText = () => {
  // AOS 
  useEffect(() => {
    import('aos').then((AOS) => {
      AOS.init();
    });
  }, []);

  return (
    <div className="div-margin">
      <div
        data-aos="fade-up"
        data-aos-duration="1500"
        data-aos-delay="200"
      >
        <p>     </p>
        <p>   </p>
        <p>     </p>
      </div>

      <div
        data-aos="fade-up"
        data-aos-duration="1500"
        data-aos-delay="200"
      >
        <p className="extra-space">      </p>
        <p>     </p>
      </div>

      <div
        data-aos="fade-up"
        data-aos-duration="1500"
        data-aos-delay="200"
      >
        <div className="extra-space">
          <p> .   </p>
          <p> .   </p>
        </div>
      </div>
    </div>
  );
};

export default InvitationText;
