import React, { useEffect, useState } from 'react';
import dayjs from 'dayjs';
import AOS from 'aos';
import 'aos/dist/aos.css';
import './WeddingDatePicker.css';

const WeddingDatePicker = () => {
    const [selectedDate, setSelectedDate] = useState(null); //    
    const weddingDate = dayjs('2025-02-22T16:30:00'); //  
    const monthStart = weddingDate.startOf('month'); //    
    const monthEnd = weddingDate.endOf('month'); //   

    const monthData = []; //    

    //     
    for (let day = monthStart; day.isBefore(monthEnd.add(1, 'day')); day = day.add(1, 'day')) {
        monthData.push(day);
    }

    //       
    const weeks = [];
    const firstDayOfWeek = monthStart.day(); //   

    //     
    const firstWeek = Array(firstDayOfWeek).fill(null).concat(monthData.slice(0, 7 - firstDayOfWeek));
    weeks.push(firstWeek);

    //   
    for (let i = 7 - firstDayOfWeek; i < monthData.length; i += 7) {
        weeks.push(monthData.slice(i, i + 7));
    }

    //       
    const lastWeek = weeks[weeks.length - 1];
    const lastWeekLength = lastWeek.length;

    //       
    if (lastWeekLength < 7) {
        const emptyDays = Array(7 - lastWeekLength).fill(null); //    
        weeks[weeks.length - 1] = lastWeek.concat(emptyDays); //     
    }

    //   1 
    if (lastWeek[lastWeekLength - 1]?.date() === 1) {
        weeks[weeks.length - 1][lastWeekLength - 1] = null; //   1 null 
    }

    // /  
    const hour = weddingDate.hour();
    const period = hour < 12 ? '' : '';

    //   
    const wddingHour = weddingDate.format('h'); //  (1~12 )
    const minute = weddingDate.format('mm'); // 
    const weddingTime = `${period} ${wddingHour}:${minute}`; //    

    //   
    const monthNames = [
        '', '', '', '', '', '', '', '', '', '', '', ''
    ];

    //   
    const dayNames = [
        ' ', ' ', ' ', ' ', ' ', ' ', ' ',
        ' ', ' ', ' ', ' ', ' ', ' ', ' ',
        ' ', ' ', ' ', ' ', ' ', ' ',
        ' ', ' ', ' ', ' ', ' ', ' ',
        ' ', ' ', ' ', ' '
    ];

    // AOS 
    useEffect(() => {
        AOS.init({
            duration: 800, //  
            once: true, //    
        });
    }, []);

    //   
    const handleDateClick = (day) => {
        if (day) {
            setSelectedDate(day.format('YYYY-MM-DD')); //     
        }
    };

    return (
        <div data-aos="fade-up" data-aos-duration="1500" data-aos-delay="200">
            <div className="schedule-calandar-wrapper">
                <div className="wedding-date red-text" >
                    {monthNames[weddingDate.month()]}<br />
                    {dayNames[weddingDate.date() - 1]}.
                </div>
                <div className="common-calandar"> {/*   AOS  */}
                    <div className="common-calandar-table">
                        <div className="common-calandar-header"></div>
                        <div className="common-calandar-week">
                            <div className="common-calandar-day sunday"></div>
                            <div className="common-calandar-day monday"></div>
                            <div className="common-calandar-day tuesday"></div>
                            <div className="common-calandar-day wednesday"></div>
                            <div className="common-calandar-day thursday"></div>
                            <div className="common-calandar-day friday"></div>
                            <div className="common-calandar-day saturday"></div>
                        </div>
                        {weeks.map((week, weekIndex) => (
                            <div key={weekIndex} className="common-calandar-week">
                                {week.map((day, dayIndex) => {
                                    const isWeddingDay = day && day.isSame(weddingDate, 'day'); //   
                                    return (
                                        <div
                                            key={dayIndex}
                                            className={`common-calandar-date ${isWeddingDay ? 'circle' : ''}`}
                                            onClick={() => handleDateClick(day)} //      
                                        >
                                            <span>{day ? day.date() : ''}</span>
                                            {isWeddingDay && (
                                                <span className="wedding-day-text">
                                                    {weddingTime} {/*    */}
                                                </span>
                                            )}
                                        </div>
                                    );
                                })}
                            </div>
                        ))}
                        <div className="common-calandar-header"></div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default WeddingDatePicker;
